body {
  font-family:monospace;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input[type="text"],
input[type="number"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

select {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  background-color: #007BFF;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
/* Product Card Container */
.product-card {
  width: 300px;
  border: 1px solid #ddd;
  border-radius: 15px;
  padding: 10px;
  margin: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  text-align: center;
}

/* Product Title */
.product-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Product Price */
.product-price {
  font-size: 16px;
  color: #007BFF;
}

/* Product Discount Percentage */
.product-discount {
  font-size: 14px;
  color: #e74c3c; /* Red color for discounts */
}

/* Product Rating */
.product-rating {
  font-size: 14px;
  color: #f1c40f; /* Yellow color for ratings */
}

/* Product Brand */
.product-brand {
  font-size: 14px;
  margin-top: 5px;
}

/* Product Category */
.product-category {
  font-size: 14px;
  color: #3498db; /* Blue color for categories */
}

/* Product Thumbnail */
.product-thumbnail {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
}

/* Product Image */
.product-image {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
}

/* Hover effect for the card */
.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.product-list{
  display: flex;
  flex-wrap: wrap;
}